import React, {useEffect, useState} from 'react';
import styled from "styled-components";
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import {BASE_URL, getHeaders, mainColor, TOKEN, TokenType} from "../../../utills/ServiceUrls";
import axios from "axios";
import {toast} from "react-toastify";
import {AiOutlineCloudDownload, AiOutlineCloudUpload} from "react-icons/ai";
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import {USERINFO_BASE64} from "../../../utills/fileBase64";
import FileSaver from 'file-saver'
import {extrasmall, medium, small} from "../../../responsiv";
import Modal from "@mui/material/Modal";
import {RiCloseLine} from "react-icons/ri";
import {motion as m} from "framer-motion";
import {DataGrid, GridToolbar} from "@mui/x-data-grid";
import SaveUserForm from "../../form/SaveUserForm";
import {useSelector} from "react-redux";
import ButtonMui from "@mui/material/Button";
import {BiSolidUserPlus} from "react-icons/bi";
import {FaEdit, FaTrash} from "react-icons/fa";

const AdminUserAddPage = () => {


    const [show, setShow] = useState(false);
    const [openBg, setOpenBg] = React.useState(false);
    const [spinner, setSpinner] = useState(true);
    const [open, setOpen] = useState(false);
    const {headers} = getHeaders();
    const columns = [
        {field: 'id', headerName: 'ID', width: 40, editable: false},
        {field: 'fullName', headerName: 'Full Name', type: 'string', width: 300, editable: false,},
        {field: 'lastName', headerName: 'Last Name', type: 'string', width: 200, editable: false,},
        {field: 'firstName', headerName: 'First Name', type: 'string', width: 200, editable: false,},
        {field: 'middleName', headerName: 'Middle Name', type: 'string', width: 200, editable: false,},
        {field: 'login', headerName: 'Login', type: 'string', width: 150, editable: false,},
        {field: 'rfid', headerName: 'RFID', type: 'string', width: 150, editable: false,},
        {field: 'passport', headerName: 'Passport', type: 'string', width: 120, editable: false,},
        {field: 'password', headerName: 'Password', type: 'string', width: 100, editable: false,},
        {field: 'gander', headerName: 'Gander', type: 'string', width: 100, editable: false,},
        {
            field: 'state', headerName: 'State', type: 'string', width: 240, editable: false,
            renderCell: (cellValues) => {
                return (
                    <Wrapper2>
                        <Button
                            bg={"blue"}
                            variant="contained"
                            onClick={(e) => {
                                e.preventDefault();
                                getUpdateItem(cellValues.id);
                            }}
                        >
                            <FaEdit />
                            Update
                        </Button>

                        <Button
                            bg={"red"}
                            variant="contained"
                            color="danger"
                            onClick={(e) => {
                                e.preventDefault();
                                handleDeleted(cellValues.id, cellValues?.row?.firstName, cellValues?.row?.lastName);
                            }}
                        >
                            <FaTrash />
                            Delete
                        </Button>
                    </Wrapper2>
                );
            }
        },
    ];
    const [data, setData] = useState([
        {id: 1, name: "Tohir Asadov", state: "123456",},
        {id: 2, name: "Tohir Asadov", state: "123456",},
    ])
    const [editUser, setEditUser] = useState({})
    const [edit, setEdit] = useState(false);

    const user = useSelector(state => state?.user?.user);

    const getUpdateItem = (id) => {
        let item = data?.find(i => i?.id === id);
        setEditUser(prevState => ({
            ...prevState,
            id: item?.id,
            firstName: item?.firstName,
            lastName: item?.lastName,
            middleName: item?.middleName,
            login: item?.login,
            rfid: item?.rfid,
            passport: item?.passport,
            password: item?.password,
            gander: item?.ganderId === 1 ? "MALE" : "FEMALE",
            roles: item?.roles,
            positions: item?.positions,
            address: item?.address?.address,
            region: item?.address?.region,
            district: item?.address?.district,
        }))
        setEdit(true)
        console.log(item, "get item--------------------------------------")
    }

    const handleDeleted = (id, name, surename) => {

        if (window.confirm(`Do you want to delete ${surename} ${name}?`)) {
            axios.delete(`${BASE_URL}/user/deleteUser/${id}`, {headers})
                .then(res => {
                    toast.success(res?.data?.message, {
                        position: "top-right",
                        autoClose: false,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                        theme: "colored",
                    })
                })
                .catch(err => {
                    console.log(err)
                })
            toast.success("Deleted successfully", {
                position: "top-right",
                autoClose: false,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "colored",
            })
            setSearch("")
            setData([])
        }

    }
    const savePhoto = (e) => {
        const url = BASE_URL + "/userinfo/uploadUserInfoAndUser";
        const formData = new FormData();
        formData.append('file', e.target.files[0]);
        const token = localStorage.getItem(TOKEN)
        const config = {
            headers: {
                'Content-Type': 'multipart/form-data',
                'Authorization': TokenType + token,
                'Access-Control-Allow-Origin': '*'
            }
        }

        setOpen(true)
        setOpenBg(true);


        axios.post(url, formData, config)
            .then((response) => {
                toast.success(response?.data?.message, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "colored",
                })
                setOpenBg(false)
                setOpen(false)
            })
            .catch(err => {
                setOpenBg(false)
                setOpen(false)

                toast.error(err?.response?.data?.message, {
                    position: "top-right",
                    autoClose: false,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "colored",
                })
            })

    }

    const downloadFileFromBase64 = () => {
        let dataFile = USERINFO_BASE64;
        let sliceSize = 1024;
        let byteCharacters = atob(dataFile);
        let bytesLength = byteCharacters.length;
        let slicesCount = Math.ceil(bytesLength / sliceSize);
        let byteArrays = new Array(slicesCount);
        for (let sliceIndex = 0; sliceIndex < slicesCount; ++sliceIndex) {
            let begin = sliceIndex * sliceSize;
            let end = Math.min(begin + sliceSize, bytesLength);
            let bytes = new Array(end - begin);
            for (var offset = begin, i = 0; offset < end; i++, offset++) {
                bytes[i] = byteCharacters[offset].charCodeAt(0);
            }
            byteArrays[sliceIndex] = new Uint8Array(bytes);
        }
        let blob = new Blob(byteArrays, {type: 'application/vnd.ms-excel'});
        FileSaver.saveAs(new Blob([blob], {}), "USERINFO.xlsx");
    }

    const handleClose = () => {
        setShow(false);
        setEdit(false);
    }


    const onUpdate = (form) => {
        Object.values(form).every(
            value => value !== null && value
        )
            ?
            axios.post(BASE_URL + "/admin/saveUser", {...form}, {headers})
                .then(res => {
                    setEdit(false);
                    toast.success(res?.data?.message);
                })
                .catch(err => {
                    toast.error(err?.response?.data?.message);
                })
            : toast.warning("Empty eny field..");


    }

    const onSave = (form) => {
        delete form['id'];
        console.log(Object.values(form).every(
            value => value !== null && value
        ), "valed")
        console.log({...form, id: null}, "submit form")

        Object.values(form).every(
            value => value !== null && value
        )
            ? axios.post(BASE_URL + "/admin/saveUser", {...form}, {headers})
                .then(res => {
                    // console.log(res,"save successfully")
                    setShow(false);
                    toast.success(res?.data?.message);
                })
                .catch(err => {
                    // console.log(err,"save error..........")
                    toast.error(err?.response?.data?.message);
                })
            : toast.warning("Empty eny field..");

    }

    const [forData, setForData] = useState(null);
    const [search, setSearch] = useState("");

    const handleSearch = (e) => {
        e.preventDefault();
        setSearch(e?.target?.value);
    }

    useEffect(() => {
        console.log(editUser, "edit user")
    }, [editUser])

    useEffect(() => {
        if (search?.length > 4) {
            axios.get(`${BASE_URL}/admin/param?param=${search}`)
                .then(res => {
                    setData(res?.data?.obj);
                    setSpinner(false)
                })
                .catch(err => {
                    console.log(err)
                })
        }

    }, [search])

    useEffect(() => {
        axios.get(BASE_URL + "/admin/userAdd?id=" + user?.id)
            .then(res => {
                setForData(res?.data)
            })
            .catch(err => {
                console.log(err)
            })
    }, [])

    return (
        <Container>

            <Wrapper>

                <ButtonMui
                    variant={'contained'}
                    component={'label'}
                    endIcon={<AiOutlineCloudUpload/>}
                >
                    Import USERS from file
                    <input
                        onChange={savePhoto}
                        type="file"
                        hidden
                        accept={"application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"}
                    />
                </ButtonMui>

                <ButtonMui
                    endIcon={<AiOutlineCloudDownload/>}
                    variant={'contained'}
                    onClick={() => downloadFileFromBase64()}>
                    Export userinfo from file example
                </ButtonMui>

                <ButtonMui
                    variant={'contained'}
                    color={'success'}
                    endIcon={<BiSolidUserPlus/>}
                    onClick={() => setShow(!show)}>
                    Create USER</ButtonMui>

            </Wrapper>

            <Row>

                <TextField
                    id={search?.length > 4 ? "outlined-basic" : "standard-error-helper-text"}
                    error={search?.length > 0 && search?.length < 5}
                    label="Search user.."
                    variant={"outlined"}
                    value={search}
                    sx={{
                        width: "250px",
                        marginBottom: "10px",
                        " @media only screen and  (max-width: 576px)": {width: 300}
                    }}
                    helperText={search?.length > 0 && search?.length < 5 && "Enter a word longer than 4"}
                    onChange={handleSearch}
                />
            </Row>


            <Backdrop
                sx={{color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1}}
                open={openBg}
                // onClick={handleClose}
            >
                <CircularProgress color="inherit"/>
            </Backdrop>

            {
                spinner ? <div></div> : <DataGrid
                    checkboxSelection
                    style={{width: "500px!important", minHeight: "300px!important"}}
                    columns={columns}
                    rows={data}
                    components={{Toolbar: GridToolbar}}/*** print and excel ****/
                    autoHeight
                    pageSize={50}
                    initialState={{ // hide items
                        ...data.initialState,
                        columns: {
                            columnVisibilityModel: {
                                id: false,
                                fullName: false,
                                password: false,
                                gander: false,
                            },
                        },
                    }}
                />
            }


            <Modal
                open={show}
                onClose={handleClose}
                aria-labelledby="keep-mounted-modal-title"
                aria-describedby="keep-mounted-modal-description"
            >

                <Box sx={style} component={styleAdd}>
                    <CloseMyButtonForChild onClick={handleClose}
                                           whileHover={{rotate: 180, scale: 1.1}}
                                           whileTap={{scale: 0.9}}
                                           transition={{duration: 0.3}}
                    ><RiCloseLine/></CloseMyButtonForChild>

                    <SaveUserForm
                        title={editUser?.id !== null ? "Edit Teacher" : "Add Teacher"}
                        // title={"Add Teacher"}
                        roles={forData?.roles}
                        positions={forData?.positions}
                        regions={forData?.regions}
                        districts={forData?.districts}
                        formArr={[
                            {
                                label: "First Name",
                                name: "firstName",
                                placeholder: "First Name",
                                type: "text",
                            },
                            {
                                label: "Last Name",
                                name: "lastName",
                                placeholder: "Last Name",
                                type: "text",
                            },
                            {
                                label: "Middle Name",
                                name: "middleName",
                                placeholder: "Middle Name",
                                type: "text",
                            },
                            {
                                label: "RFID",
                                name: "rfid",
                                placeholder: "RFID",
                                type: "text",
                            },
                            {
                                label: "Login",
                                name: "login",
                                placeholder: "Login",
                                type: "text",
                            },
                            {
                                label: "Passport",
                                name: "passport",
                                placeholder: "Passport",
                                type: "text",
                            },
                            {
                                label: "Password",
                                name: "password",
                                placeholder: "Password",
                                type: "text",
                            },
                            {
                                label: "Gander",
                                name: "gander",
                                placeholder: "Gander",
                                type: "select",
                            },
                            {
                                label: "Region",
                                name: "region",
                                placeholder: "Region",
                                type: "select",
                            },
                            {
                                label: "District",
                                name: "district",
                                placeholder: "District",
                                type: "select",
                            },
                            {
                                label: "Address",
                                name: "address",
                                placeholder: "Address",
                                type: "text",
                            },
                            {
                                label: "Roles",
                                name: "roles",
                                placeholder: "Roles",
                                type: "multi",
                            },
                            {
                                label: "Positions",
                                name: "positions",
                                placeholder: "Positions",
                                type: "multi",
                            },
                        ]}
                        submitBtn={"Save"}
                        onSubmit={(form) => (onSave(form))}
                    />

                </Box>
            </Modal>


            <Modal
                open={edit}
                onClose={handleClose}
                aria-labelledby="keep-mounted-modal-title"
                aria-describedby="keep-mounted-modal-description"
            >

                <Box sx={style} component={styleAdd}>
                    <CloseMyButtonForChild onClick={handleClose}
                                           whileHover={{rotate: 180, scale: 1.1}}
                                           whileTap={{scale: 0.9}}
                                           transition={{duration: 0.3}}
                    ><RiCloseLine/></CloseMyButtonForChild>

                    <SaveUserForm
                        title={editUser?.id !== null ? "Edit Teacher" : "Add Teacher"}
                        // title={"Add Teacher"}
                        roles={forData?.roles}
                        positions={forData?.positions}
                        regions={forData?.regions}
                        districts={forData?.districts}
                        formArr={[
                            {
                                label: "First Name",
                                name: "firstName",
                                placeholder: "First Name",
                                type: "text",
                            },
                            {
                                label: "Last Name",
                                name: "lastName",
                                placeholder: "Last Name",
                                type: "text",
                            },
                            {
                                label: "Middle Name",
                                name: "middleName",
                                placeholder: "Middle Name",
                                type: "text",
                            },
                            {
                                label: "RFID",
                                name: "rfid",
                                placeholder: "RFID",
                                type: "text",
                            },
                            {
                                label: "Login",
                                name: "login",
                                placeholder: "Login",
                                type: "text",
                            },
                            {
                                label: "Passport",
                                name: "passport",
                                placeholder: "Passport",
                                type: "text",
                            },
                            {
                                label: "Password",
                                name: "password",
                                placeholder: "Password",
                                type: "text",
                            },
                            {
                                label: "Gander",
                                name: "gander",
                                placeholder: "Gander",
                                type: "select",
                            },
                            {
                                label: "Region",
                                name: "region",
                                placeholder: "Region",
                                type: "select",
                            },
                            {
                                label: "District",
                                name: "district",
                                placeholder: "District",
                                type: "select",
                            },
                            {
                                label: "Address",
                                name: "address",
                                placeholder: "Address",
                                type: "text",
                            },
                            {
                                label: "Roles",
                                name: "roles",
                                placeholder: "Roles",
                                type: "multi",
                            },
                            {
                                label: "Positions",
                                name: "positions",
                                placeholder: "Positions",
                                type: "multi",
                            },
                        ]}
                        submitBtn={(editUser?.id !== null && editUser?.id) ? "Update" : "Save"}
                        // submitBtn="Save"
                        onSubmit={(form) => (editUser?.id !== null && editUser?.id) ? onUpdate(form) : onSave(form)}
                        updateItem={editUser}
                    />


                </Box>
            </Modal>


        </Container>
    );
};


const Wrapper2 = styled.div`
    width: 100%;
    display: flex;
    gap: 10px;
    margin: 5px !important;
`;

const Button = styled.button`
    margin-top: 5px;
    float: ${props => props.float};
    width: ${props => props.width ? props.width : "100%"};
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: #ffffff;
    gap: 10px;
    background-color: ${props => props.bg};
    padding: ${props => props.padding ? props.padding : "5px 10px"};
    font-size: 16px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.2s ease;
    letter-spacing: 1.2px;

    &:focus {
        transform: scale(0.95);
    }
`;

const CloseMyButtonForChild = styled(m.button)`
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: ${mainColor};
    border-radius: 50%;
    color: white;
    font-size: 26px;
    border: none;
    position: absolute;
    top: 10px;
    right: 10px;
`;


const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 1100,
    padding: "20px",
    bgcolor: 'background.paper',
    boxShadow: 24,
    borderRadius: 3,
    p: 4,
};
const styleAdd = styled.div`
    ${medium({
        width: "97vw !important",
        height: "97vh !important"
    })}
    ${small({
        width: "97vw !important",
        height: "97vh !important"
    })}
    ${extrasmall({
        width: "97vw !important",
        height: "97vh !important"
    })}`

const Label = styled.label`
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    cursor: pointer;
    background-color: ${mainColor};
    color: white;
    padding: 20px 10px !important;
    border-radius: 8px;


    & > input:disabled {
        cursor: not-allowed;
    }
`

const Btn = styled.button`
    width: 250px;
    align-self: start;
    border: 1px solid ${mainColor};
    background-color: #fff;
    color: ${mainColor};
    border-radius: 0.5rem;
    padding: 20px 10px !important;
    letter-spacing: 1.1px;

    &:hover {
        background-color: ${mainColor};
        color: white;
    }

    &:disabled {
        cursor: not-allowed;
    }
`

const Row = styled.div`
    display: flex;
`

const Wrapper = styled.div`
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
    margin-bottom: 20px;
    ${extrasmall({
        justifyContent: "center"
    })}
`;

const Container = styled.div`
    padding: 10px !important;
`;


export default AdminUserAddPage;